import React from 'react';
import PropTypes from 'prop-types';

import { isFunction } from '@webui-one/core/utils/functional';
import { isObject, omitKeys } from '@webui-one/core/utils/object';
import { isString } from '@webui-one/core/utils/string';
import { UNSAFEWindowLocationHref } from '@webui-one/core/utils/unsafe';

class Link extends React.Component {
    constructor(props) {
        super(props);
        this.onClick = this.onClick.bind(this);
    }

    onClick(evt) {
        const { setHref, to } = this.props;
        let href = '';
        if (isFunction(setHref)) {
            if (isString(to)) {
                href = to;
            }
            if (isObject(to) && isString(to.pathname)) {
                href = to.pathname;
            }
            evt.preventDefault();
            setHref(href);
        }
    }

    render() {
        const { children, style, to, ...props } = omitKeys(this.props, ['setHref']);
        let href = '';
        if (isString(to)) {
            href = to;
        }
        if (isObject(to) && isString(to.pathname)) {
            href = to.pathname;
        }
        props.onClick = this.onClick;
        if (isObject(style)) {
            props.style = style;
        }

        return <a {...props} href={href}>{children}</a>;
    }
}

Link.defaultProps = {
    setHref: UNSAFEWindowLocationHref,
};

const LocationPropType = PropTypes.shape({
    location: PropTypes.string,
});

Link.propTypes = {
    children: PropTypes.node,
    setHref: PropTypes.func,
    style: PropTypes.shape({}),
    to: PropTypes.oneOfType([
        PropTypes.string,
        LocationPropType,
    ]),
};

export default Link;
