import { useEffect, useState } from 'react';
import {
    getDescription as getDescriptionFunc,
    getLastTh as getLastThFunc,
    getSchema as getSchemaFunc,
} from '@webui-one/core/jsonrpc';
import * as S from '@webui-one/core/schema';
import { flatten } from '@webui-one/core/utils/array';
import {
    isObject,
    stringifyObject,
} from '@webui-one/core/utils/object';
import { isNonEmptyString } from '@webui-one/core/utils/string';

export const initialState = {
    error: undefined,
    infos: undefined,
    loadStatus: undefined,
};

export const loadStatus = {
    COMPLETE: 'COMPLETE',
    ERROR: 'ERROR',
    LOADING: 'LOADING',
};

const EMPTY_ARRAY = Object.freeze([]);
const loadingInfos = state => state && state.loadStatus === loadStatus.LOADING;

const hasDescription = schema => S.flagIsSet('CS_HAS_DESCR_POS', schema);
const hasInfos = state => isObject(state) && Array.isArray(state.infos);
const selectInfos = state => (hasInfos(state) ? state.infos : EMPTY_ARRAY);

const skipKeysPredicate = ([k]) => !['name', 'list_type'].some(k1 => k === k1);

const getTypeTitle = (unionIndex, title = 'Type') => {
    if (unionIndex !== undefined) {
        return `Union (${unionIndex + 1}) ${title}`;
    }
    return title;
};

const getAdditionalTypeInfo = (types, unionIndex) => types.reduce((acc, cur) => {
    if (cur.leaf_type) {
        const leafTypeInfo = getAdditionalTypeInfo(cur.leaf_type);
        return [...acc, ...leafTypeInfo];
    }
    if (cur.union) {
        acc.push({
            title: 'Union',
            content: 'Yes',
        });
        const unions = flatten(cur.union.map(getAdditionalTypeInfo));
        return [...acc, ...unions];
    }
    if (cur.enumeration) {
        const enums = cur.enumeration.map(({ info, label }) =>
            (label + (info ? ` - ${info}` : '')),
        );
        acc.push({
            title: getTypeTitle(unionIndex, 'Enum'),
            content: enums.join('\n'),
        });
        return acc;
    }
    const buildInType = S.getBuildInType(cur.name);
    if (buildInType) {
        acc.unshift({
            title: getTypeTitle(unionIndex),
            content: `${cur.name}, ${buildInType}`,
        });
        return acc;
    }
    if (cur.range) {
        acc.push({
            title: getTypeTitle(unionIndex, 'Range'),
            content: S.formatRange(cur),
        });
        return acc;
    }
    const typeExtras = Object.entries(cur)
        .filter(skipKeysPredicate)
        .map(([k, v]) => {
            const value = isObject(v) && 'value' in v ? v.value : v;
            return ({
                title: getTypeTitle(unionIndex, k),
                content: stringifyObject(value, ', '),
            });
        });
    return [...acc, ...typeExtras];
}, []);

const getTypeInfo = (schema) => {
    const typeName = S.typeName(schema);
    const types = S.types(schema);
    if (!typeName) return [];
    if (S.isPrimitive(schema)) {
        const info = S.getBuildInType(typeName);
        return [{
            title: 'Type',
            content: `${typeName}, ${info}`,
        }];
    }
    return getAdditionalTypeInfo(types);
};
const formatAccess = access => Object.entries(access)
    .filter(([, value]) => value)
    .map(([key]) => key).join(', ');
const formatArray = a => a.join(', ');
const predTruty = a => !!a;
const infoMapper = schema => ({
    getFn,
    title,
    formatFn = a => a,
    predicateFn = value => value !== undefined,
}) => {
    const result = getFn(schema);
    if (predicateFn(result)) {
        return {
            title,
            content: `${formatFn(result)}`,
        };
    }
    return undefined;
};

const INFO_MAP = [{
    getFn: S.name,
    title: 'Name',
}, {
    getFn: S.keypath,
    title: 'Path',
}, {
    getFn: S.access,
    title: 'Access',
    formatFn: formatAccess,
}, {
    getFn: S.kind,
    title: 'Kind',
}, {
    getFn: S.info,
    title: 'Information',
    predicateFn: predTruty,
}, {
    getFn: S.description,
    title: 'Description',
    predicateFn: predTruty,
}, {
    getFn: S.key,
    title: 'List key',
    formatFn: formatArray,
}, {
    getFn: S.leafrefTargets,
    title: 'Leafref target',
    formatFn: formatArray,
}, {
    getFn: S.defaultValue,
    title: 'Default value',
}, {
    getFn: S.minElements,
    title: 'Min elements',
}, {
    getFn: S.maxElements,
    title: 'Max elements',
}, {
    getFn: S.units,
    title: 'Units',
    predicateFn: predTruty,
}];

export const getInfoArray = (schema) => {
    if (!schema) {
        return [];
    }
    const infos = INFO_MAP.map(infoMapper(schema)).filter(a => a);
    const typeInfos = getTypeInfo(schema);
    return [...infos, ...typeInfos];
};

export default function useInfos({
    path,
    funcs: {
        AbortCtrl = AbortController,
        getDescription = getDescriptionFunc,
        getLastTh = getLastThFunc,
        getSchema = getSchemaFunc,
    } = {},
} = {}) {
    const [state, setState] = useState(initialState);

    useEffect(() => {
        const updateState = (newState) => {
            setState(prevState => ({ ...prevState, ...newState }));
        };

        let controller;
        async function getInfos() {
            controller = new AbortCtrl();
            const signal = controller.signal;
            updateState({
                loadStatus: loadStatus.LOADING,
                infos: undefined,
            });
            try {
                const th = await getLastTh({ signal });
                const schema = await getSchema({
                    levels: 0,
                    path,
                    signal,
                    th,
                });
                const infos = getInfoArray(schema);
                if (hasDescription(schema)) {
                    const response = await getDescription({
                        path,
                        signal,
                        th,
                    });
                    if (response.description !== '') {
                        infos.push({
                            title: 'Description',
                            content: response.description,
                        });
                    }
                }
                if (!signal.aborted) {
                    updateState({ loadStatus: loadStatus.COMPLETE, infos });
                }
            } catch (error) {
                if (!signal.aborted) {
                    updateState({
                        loadStatus: loadStatus.ERROR,
                        error,
                        infos: undefined,
                    });
                }
            }
        }

        if (isNonEmptyString(path)) {
            getInfos();
        }

        return () => {
            if (controller) {
                controller.abort();
            }
        };
    }, [AbortCtrl, getDescription, getLastTh, getSchema, path]);
    return state;
}

export {
    EMPTY_ARRAY,
    getAdditionalTypeInfo as _getAdditionalTypeInfo,
    getTypeInfo as _getTypeInfo,
    hasDescription,
    INFO_MAP as _INFO_MAP,
    loadingInfos,
    hasInfos,
    selectInfos,
};
