import {
    login as jsonrpcLogin,
    challengeResponse as _challengeResponse,
} from '@webui-one/core/jsonrpc';

import {
    expandQueryString,
} from '@webui-one/core/utils/path';

import { hasValidSession } from '@webui-one/core/utils/session';

import { UNSAFEWindowLocationHref } from '@webui-one/core/utils/unsafe';
import * as actionTypes from './actionTypes';
import { filterOnLogin } from './storage-filters';

export const loginPendingAction = () => ({
    type: actionTypes.LOGIN_PENDING,
});

export const loginSuccessAction = () => ({
    type: actionTypes.LOGIN_SUCCESS,
});
export const loginClearErrorAction = () => ({
    type: actionTypes.LOGIN_CLEAR_ERROR,
});
export const loginErrorAction = error => ({
    type: actionTypes.LOGIN_ERROR,
    error,
});

export const loginSessionCheckStartAction = () => ({
    type: actionTypes.LOGIN_SESSION_CHECK_START,
});
export const loginSessionCheckCompleteAction = sessionStatus => ({
    type: actionTypes.LOGIN_SESSION_CHECK_COMPLETE,
    sessionStatus,
});
export const loginSessionCheckErrorAction = error => ({
    type: actionTypes.LOGIN_SESSION_CHECK_ERROR,
    error,
});

export const redirectToWebui = () => {
    filterOnLogin();
    const params = expandQueryString(window.location.search);
    UNSAFEWindowLocationHref(params.oldUrl || '/index.html');
};

const loginSuccess = (dispatch) => {
    dispatch(loginSuccessAction());
    redirectToWebui();
};

export const checkSession = ({
    funcs: {
        hasSession = hasValidSession,
    } = {},
} = {}) => async (dispatch) => {
    dispatch(loginSessionCheckStartAction());
    try {
        const sessionStatus = await hasSession();
        dispatch(loginSessionCheckCompleteAction(sessionStatus));
    } catch (error) {
        dispatch(loginSessionCheckErrorAction(error));
    }
};

export const login = (
    user,
    passwd,
    {
        funcs: {
            loginFunc = jsonrpcLogin,
        } = {},
    } = {},
) => async (dispatch) => {
    dispatch(loginPendingAction());
    try {
        await loginFunc({ user, passwd });
        loginSuccess(dispatch);
    } catch (error) {
        dispatch(loginErrorAction(error));
    }
};
export const challenge = ({
    challengeId,
    response,
    funcs: {
        challengeResponse = _challengeResponse,
        loginSuccessFn = loginSuccess,
    } = {},
} = {},
) => async (dispatch) => {
    dispatch(loginPendingAction());
    try {
        await challengeResponse({
            challengeId,
            response,
            ackWarning: false,
        });
        loginSuccessFn(dispatch);
    } catch (error) {
        dispatch(loginErrorAction(error));
    }
};
