import React from 'react';
import PropTypes from 'prop-types';
import MessageBox from './MessageBox';
import { stringifyObject } from '@webui-one/core/utils/object';

const ERROR_KEYS = [
    'message',
    'reason',
    'data',
    'type',
    'internal',
    'code',
];
const LABELS = {
    message: 'Error',
};
export default function ErrorBox({ error }) {
    return (
        <MessageBox name="error-message">
            <span role="img" aria-label="warning"></span>
            {ERROR_KEYS.filter(k => k in error).map(k =>
                `${LABELS[k] || k}: ${stringifyObject(error[k])}`).join('. ')
            }
        </MessageBox>
    );
}

ErrorBox.propTypes = {
    error: PropTypes.shape(Object),
};
