import {
    deleteNodes as _deleteNodes,
    load as _load,
    showConfig as _showConfig,
} from '@webui-one/core/jsonrpc';
import {
    isObject,
} from '@webui-one/core/utils/object';
import {
    defaultTo,
} from '@webui-one/core/utils/functional';
import { quoteKey } from '@webui-one/core/utils/key';

import {
    APP_DATA_PATH,
    APP_IDS,
    APP_PATH,
} from './constants';

export const dataOrDefault = (data, keys, defaultValue) => (
    defaultTo(defaultValue, keys.reduce((acc, cur) =>
        ((!isObject(acc) && !Array.isArray(acc)) ?
            undefined :
            acc[cur]), data))
);

export const loadApplications = async ({
    th,
    funcs: {
        showConfig = _showConfig,
    } = {},
} = {}) => {
    const data = await showConfig({
        th,
        resultAs: 'json',
        withOper: false,
        path: APP_PATH,
    });

    const allApps = dataOrDefault(data, APP_DATA_PATH, []);
    const applications = allApps.filter(({ id }) => APP_IDS.includes(id));
    return {
        applications,
    };
};

export const saveApplication = async ({
    th,
    data,
    isDefault,
    funcs: {
        load = _load,
        deleteNodes = _deleteNodes,
    } = {},
} = {}) => {
    const mergeData = {
        'tailf-webui:webui': {
            'tailf-webui-one:applications': {
                application: data,
            },
        },
    };
    const delPaths = data.map(({ id }) => `${APP_PATH}{${quoteKey(id)}}`);
    const response = await deleteNodes({ th, paths: delPaths });
    if (isDefault) return response;
    return load({
        data: mergeData,
        format: 'json',
        mode: 'merge',
        th,
    });
};
