import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { selectIncludeSubfolders } from './reducer';
import { includeSubfoldersAction } from './actions';
import ToggleButtonContainer from './ToggleButtonContainer';
import FlattenIcon from './static/FlattenIcon';

class FlattenButtonContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onToggle = this.onToggle.bind(this);
    }

    onToggle() {
        const { isFlattened, onSetFlatten } = this.props;
        onSetFlatten(!isFlattened);
    }

    render() {
        const { isFlattened, shortcut } = this.props;
        return (
            <ToggleButtonContainer
                dataHandle="flattenButton"
                isChecked={isFlattened}
                onToggle={this.onToggle}
                shortcut={shortcut}
                toolTipText="include subfolders"
            >
                <FlattenIcon />
            </ToggleButtonContainer>
        );
    }
}

FlattenButtonContainer.propTypes = {
    isFlattened: PropTypes.bool,
    onSetFlatten: PropTypes.func,
    shortcut: PropTypes.string,
};

FlattenButtonContainer.defaultProps = {
    shortcut: 'vi',
};

const mapDispatchToProps = dispatch => ({
    onSetFlatten(value) {
        dispatch(includeSubfoldersAction(value));
    },
});

const mapStateToProps = state => (
    { isFlattened: selectIncludeSubfolders(state) }
);

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(FlattenButtonContainer);

export {
    FlattenButtonContainer as _FlattenButtonContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
