import React from 'react';
import PropTypes from 'prop-types';
import { isObject } from '@webui-one/core/utils/object';
import style from './SelectStyles';

export const OPTION_KEYS = {
    SELECT_OPTION: '-1',
    VALUE_NOT_IN_LIST: '-2',
    DEFAULT_VALUE: '-3',
};

const getPlaceholderText = (placeholder) => {
    if (typeof placeholder === 'string') return placeholder;
    return 'Select...';
};

const concatOption = (val, info) => (
    typeof info === 'string' && info !== '' ? `${val} - ${info}` : val
);

const getTextAndValue = val => ({
    optionText: isObject(val) ? val.text : val,
    optionValue: isObject(val) ? val.value : val,
});

function Select({
    autoFocus, editable, formatOption, name, options, optionsInfo, onSetValue,
    value, isLoading, isDefaultValue,
    placeholder, extraSelectStyle, extraWrapperStyle,
}) {
    const placeholderText = getPlaceholderText(placeholder);
    let content = [<option key={OPTION_KEYS.SELECT_OPTION} value="" disabled>{!isLoading && placeholderText}</option>];

    const selectValue = typeof value === 'boolean' ? `${value}` : value;
    if (isDefaultValue) {
        const formattedOption = `(${formatOption(selectValue, optionsInfo[options.indexOf(value)])})`;
        content.push((
            <option disabled value={selectValue} key={OPTION_KEYS.DEFAULT_VALUE} >
                {formattedOption}
            </option>
        ));
    }
    if (selectValue && options.indexOf(selectValue) === -1) {
        content.push((
            <option value={selectValue} key={OPTION_KEYS.VALUE_NOT_IN_LIST} >{selectValue}</option>
        ));
    }

    if (options.length) {
        content = content.concat(options.map((val, ix) => {
            const { optionText, optionValue } = getTextAndValue(val);
            return (
                <option value={optionValue} key={ix} >
                    { formatOption(optionText, optionsInfo[ix]) }
                </option>
            );
        }));
    }

    const wrapperStyle = {
        ...(editable ? style.wrapper : style.disabledWrapper),
        ...extraWrapperStyle,
    };

    const selectStyle = {
        ...(editable ? style.select : style.disabledSelect),
        ...extraSelectStyle,
    };

    return (
        <div style={wrapperStyle}>
            <select
                autoFocus={autoFocus}
                data-handle={name}
                value={selectValue}
                onChange={event => (onSetValue(event.target.value))}
                disabled={!editable}
                style={selectStyle}
            >
                { content }
            </select>
        </div>
    );
}

Select.propTypes = {
    autoFocus: PropTypes.bool,
    editable: PropTypes.bool,
    extraSelectStyle: PropTypes.shape({}),
    extraWrapperStyle: PropTypes.shape({}),
    formatOption: PropTypes.func,
    isDefaultValue: PropTypes.bool,
    name: PropTypes.string,
    options: PropTypes.arrayOf(
        PropTypes.oneOfType([
            PropTypes.string,
            PropTypes.shape({
                text: PropTypes.string,
                value: PropTypes.string,
            }),
        ]),
    ).isRequired,
    optionsInfo: PropTypes.arrayOf(PropTypes.string),
    onSetValue: PropTypes.func,
    placeholder: PropTypes.string,
    value: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.bool,
    ]),
    isLoading: PropTypes.bool,
};

Select.defaultProps = {
    editable: true,
    formatOption: concatOption,
    options: [],
    optionsInfo: [],
    value: '',
    onSetValue: () => null,
};

export default Select;

export {
    getPlaceholderText as _getPlaceholderText,
    concatOption as _concatOption,
};
