import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Input from '@webui-one/components/ConfigRender/Input/Input';
import Modal from '@webui-one/components/Modal/Modal';
import { isNativeApplication } from '@webui-one/core/defaultApplications';
import TabDeck from '@webui-one/components/TabDeck/TabDeck';
import WidgetLeaf from '@webui-one/components/ConfigRender/WidgetLeaf';
import ReadOnly from '@webui-one/components/ConfigRender/ReadOnly/ReadOnly';
import { applicationsSelector } from 'modules/applications/selectors';
import { _EditModeButtonContainer as EditModeButton } from 'ConfigurationEditor/EditModeButtonContainer';
import { _FlattenButtonContainer as IncludeSubfoldersButton } from 'ConfigurationEditor/FlattenButtonContainer';
import style from './HelpModalStyles';

const getPointer = (length, x, y, rotation) => (
    <div
        style={{
            position: 'absolute',
            top: '0',
            left: '0',
            transform: `translate(${x}rem, ${y}rem)`,
        }}
    >
        <div style={{
                transform: `rotate(${rotation}deg)`,
                transformOrigin: 'top left',
            }}
        >
            <div style={{ ...style.pointer, width: `${length}rem` }} />
        </div>
    </div>
);

