const getShortcut = (shortcut, text1, text2) => (
    <div data-handle={`shortcut-${shortcut}`} style={style.shortcut} key={shortcut}>
        <div style={style.smallText}>Press</div>
        {[...shortcut].map((key, idx) => (
            <Fragment key={`${key}${idx}`}>
                <div style={style.key}>{key.toUpperCase()}</div>
                {idx < shortcut.length - 1 && <div style={style.smallText}>and then</div>}
            </Fragment>
        ))}
        <div style={{ ...style.smallText, width: '7rem' }}>{text1}</div>
        <div style={style.largeText}>{text2}</div>
    </div>
);

const buildShortcuts = nativeApplications => (
    <div style={style.nonClickable}>
        <div style={style.header}>
            To navigate more quickly, use the following shortcuts.
            <br />
            Rule of thumb is that all default navigation shortcuts start with a G as in go to.
        </div>
        {nativeApplications.map(({ id, shortcut }) => {
            if (shortcut) {
                return getShortcut(shortcut, 'to open the', id);
            }
            return null;
        })}
        <div style={style.header}>
            To quickly change the view options, use the following shortcuts.
            <br />
            Rule of thumb is that they all start with a V as in view.
        </div>
        {getShortcut('vm', 'to toggle between', 'full model / only configuraion data')}
        <div style={style.header}>When full model is activated</div>
        {getShortcut('vo', 'to hide/show', 'operational data')}
        {getShortcut('va', 'to hide/show', 'actions')}
        <div style={style.includeSubfolders}>
            <div style={style.verticalCenter}>
                To toggle the &apos;include subfolders&apos; mode.
            </div>
            <IncludeSubfoldersButton
                onSetFlatten={() => null}
                isFlattened
                shortcut=""
            />
        </div>
        {getShortcut('vi', 'to toggle', 'include subfolders')}
        <div style={style.includeSubfolders}>
            <div style={style.verticalCenter}>
                To toggle the &apos;edit&apos; mode.
            </div>
            <EditModeButton
                onSetEditMode={() => null}
                isEditMode
                shortcut=""
            />
        </div>
        {getShortcut('vs', 'to toggle', 'edit mode')}
    </div>
);

const resources = (
    <div style={style.container}>
        <div style={style.header}>
            <ul style={style.list}>
                <li>
                    The NSO documentation and user guide can be found in the NSO
                    installation/doc folder.
                </li>
                <li>
                    The official NSO developer site, the NSO developer hub, NSO
                    on GitHub and much more:
                    <a
                        href="https://developer.cisco.com/site/nso/"
                        style={style.externalLink}
                        data-handle="developer-link"
                    >
                        https://developer.cisco.com/site/nso/
                    </a>
                </li>
            </ul>
        </div>
    </div>

);

const buildPages = nativeApplications => [
    {
        name: 'widgetLeaf',
        page: widgetLeaf,
        tab: 'leaf widget',
    },
    {
        name: 'shortcuts',
        page: buildShortcuts(nativeApplications),
        tab: 'keyboard shortcuts',
    },
    {
        name: 'resources',
        page: resources,
        tab: 'resources',
    },
];

const HelpModal = ({ onChangeTab, onRequestClose, applications }) => {
    const nativeApplications = Object.values(applications).filter(app => isNativeApplication(app));
    const pages = buildPages(nativeApplications);
    return (
        <Modal onRequestClose={onRequestClose}>
            <div style={{ margin: '-1.5rem' }}>
                <TabDeck onChangeTab={onChangeTab} tabs={pages} />
            </div>
        </Modal>
    );
};

HelpModal.propTypes = {
    applications: PropTypes.shape({}),
    onChangeTab: PropTypes.func,
    onRequestClose: PropTypes.func,
};

const mapStateToProps = state => ({
    applications: applicationsSelector(state),
});

HelpModal.defaultProps = {
    onChangeTab: () => null,
};

export default connect(
    mapStateToProps,
)(HelpModal);

