import React from 'react';
import PropTypes from 'prop-types';
import style from './ReadOnlyStyles';

const ReadOnly = ({ value, isDefaultValue }) => {
    const text = isDefaultValue ? `(${value})` : value;
    return (
        <div data-handle="read-only" style={style.readOnlyField}>
            {text}
        </div>
    );
};

ReadOnly.propTypes = {
    isDefaultValue: PropTypes.bool,
    value: PropTypes.string,
};

ReadOnly.defaultProps = {
    value: '',
};

export default ReadOnly;
