/* eslint-disable jsx-a11y/no-static-element-interactions */
import React from 'react';
import PropTypes from 'prop-types';
import { uniqConcat } from '@webui-one/core/utils/array';
import { isFunction } from '@webui-one/core/utils/functional';
import {
    isDeepEqual,
    omitKeys,
} from '@webui-one/core/utils/object';
import Modal from '../Modal/Modal';
import ToolTip from '../ToolTip/ToolTip';
import Select from '../ConfigRender/Select/Select';
import style from './FlagsButtonStyles';
import Flag from './Flag';


class FlagsButton extends React.Component {
    constructor(props) {
        super(props);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
        this.showModal = this.showModal.bind(this);
        this.onClose = this.onClose.bind(this);
        this.onClear = this.onClear.bind(this);
        this.onRevert = this.onRevert.bind(this);
        this.setFlag = this.setFlag.bind(this);
        this.flagTips = {};
        this.state = {
            modalFlags: {},
            showModal: false,
        };
    }

    handleUpdatedFlags(flags, prevFlags) {
        const { availableFlags } = this.props;
        const keys = uniqConcat(
            Object.keys(flags),
            Object.keys(prevFlags),
        );

        keys.forEach((key) => {
            if (isDeepEqual(flags[key], prevFlags[key])) {
                return;
            }
            const flag = availableFlags.find(f => f.name === key);
            if (flag && isFunction(flag.action)) {
                flag.action(flags[key]);
            }
        });
    }

    onClear() {
        const { onSetFlags } = this.props;
        this.setState({ modalFlags: {} });
        onSetFlags({});
    }

    onClose() {
        const { flags, onSetFlags } = this.props;
        const { modalFlags } = this.state;
        this.setState({ modalFlags: {}, showModal: false });
        if (!isDeepEqual(flags, modalFlags)) {
            onSetFlags(modalFlags);
        }
    }

    onRevert() {
        const { onSetFlags } = this.props;
        const { flags, modalFlags } = this.state;
        if (modalFlags !== flags) {
            this.setState({ modalFlags: flags });
            onSetFlags(flags);
        }
    }

    getFlags() {
        const { availableFlags } = this.props;
        const { modalFlags: flags } = this.state;
        const available = Array.isArray(availableFlags) ? availableFlags : [];
        const children = available.map((flag) => {
            if (Array.isArray(flag.values)) {
                return (
                    <div style={style.row} key={flag.name}>
                        <div style={style.row.title}>{flag.title}</div>
                        <Select
                            editable
                            key={flag.name}
                            name={flag.name}
                            options={['', ...flag.values]}
                            onSetValue={value => this.setFlag(flag.name, value)}
                            styleName="listStyles"
                            value={flags[flag.name]}
                        />
                    </div>
                );
            }
            return null;
        });
        return (
            <div>
                {children}
            </div>
        );
    }

    setFlag(name, value) {
        const { onSetFlags } = this.props;
        const { modalFlags: currentFlags } = this.state;
        const modalFlags = value
            ? { ...currentFlags, [name]: value }
            : omitKeys(currentFlags, [name]);
        this.setState({ modalFlags });
        onSetFlags({ ...modalFlags });
    }

    showModal() {
        const { flags } = this.props;
        this.setState({
            flags,
            modalFlags: flags,
            showModal: true,
        });
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    showTooltip() {
        this.tooltip.show();
    }

    render() {
        const { showModal } = this.state;
        return (
            <div style={style.devFlagButton.container}>
                <button
                    type="button"
                    style={style.devFlagButton.button}
                    onClick={this.showModal}
                    onMouseDown={this.showTooltip}
                    onMouseUp={this.hideTooltip}
                    onMouseEnter={this.showTooltip}
                    onMouseLeave={this.hideTooltip}
                    data-handle="devFlagsButton"
                >
                    <div style={{ width: '1rem', height: '1rem', margin: '0 .25rem', alignSelf: 'center' }}>
                        <Flag />
                    </div>
                </button>
                <ToolTip
                    direction="left"
                    toolTipText="set development flags"
                    ref={(tt) => { this.tooltip = tt; }}
                />
                {showModal && (
                    <Modal
                        title="Flags"
                        onRequestClose={this.onClose}
                    >
                        {this.getFlags()}
                        <button
                            type="button"
                            data-handle="clearBtn"
                            key="clear"
                            style={style.buttonOk}
                            onClick={this.onClear}
                        >
                            Clear
                        </button>
                        <button
                            type="button"
                            data-handle="revertBtn"
                            key="revert"
                            style={style.buttonOk}
                            onClick={this.onRevert}
                        >
                            Revert
                        </button>
                        <button
                            type="button"
                            data-handle="closeBtn"
                            key="close"
                            style={style.buttonOk}
                            onClick={this.onClose}
                        >
                            Close
                        </button>
                    </Modal>
                )}
            </div>
        );
    }
}

const availableFlagPropType = PropTypes.shape({
    name: PropTypes.string,
    title: PropTypes.string,
});


FlagsButton.propTypes = {
    availableFlags: PropTypes.arrayOf(availableFlagPropType),
    flags: PropTypes.shape({}),
    onSetFlags: PropTypes.func,
};

export default FlagsButton;

export {
    FlagsButton as _FlagsButton,
};
