/* eslint-disable import/prefer-default-export */

import {
    nodesFilter,
} from 'modules/nodes/storage-filters';
import {
    loadMetadata,
    updateStoredState,
} from '@webui-one/storage/storage';
import {
    emptyFilter,
    identityFilter,
} from '@webui-one/storage/storage-filters';
import {
    yangActionsFilter,
} from 'modules/yangActions/storage-filters';
import {
    applicationConfigurationFilter,
} from 'ConfigurationEditor/storage-filters';

export const loginStateFilters = {
    applicationConfiguration: applicationConfigurationFilter,
    comet: emptyFilter,
    userdata: emptyFilter,
    nodes: nodesFilter,
    systemSetting: emptyFilter,
    yangActions: yangActionsFilter,
    types: identityFilter,
};

export const filterOnLogin = () => {
    const state = loadMetadata();
    updateStoredState(
        state,
        {
            filters: loginStateFilters,
            defaultFilter: emptyFilter,
        },
    );
};

