import React from 'react';
import PropTypes from 'prop-types';
import style from './TabDeckStyles';

const getIndex = (index, defaultIndex) => (
    typeof index === 'undefined' ? defaultIndex : index
);

const getStyle = (tabDeckStyle, index, activeIndex, disabled) => {
    if (disabled) {
        return tabDeckStyle.disabledtab;
    }
    if (index === activeIndex) {
        return tabDeckStyle.activeTab;
    }
    return tabDeckStyle.tab;
};

const getDataHandle = (name, index, activeIndex, disabled) => {
    const isActive = index === activeIndex;
    return `tab${index}--${name}--${isActive ? '-active' : ''}${disabled ? '-disabled' : ''}`;
};

class TabDeck extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeIndex: undefined,
            focusIndex: undefined,
        };
        this.onClick = this.onClick.bind(this);
    }

    componentDidUpdate(prevProps, prevState) {
        const { defaultIndex: newIndex } = this.props;
        const { defaultIndex } = prevProps;
        const { activeIndex } = prevState;

        if (defaultIndex !== newIndex && activeIndex !== newIndex) {
            // eslint-disable-next-line react/no-did-update-set-state
            this.setState({ activeIndex: newIndex, focusIndex: newIndex });
        }
    }

    onClick(index) {
        const { onChangeTab } = this.props;
        const { activeIndex } = this.state;
        if (index !== activeIndex) {
            this.setState({ activeIndex: index, focusIndex: index });
            onChangeTab(index);
        }
    }

    render() {
        const { tabs, defaultIndex } = this.props;
        const { activeIndex } = this.state;
        const newActiveIndex = getIndex(activeIndex, defaultIndex);
        return (
            <div style={style.main}>
                <div style={style.tabContainer}>
                    { tabs.map((element, index) => (
                        <div
                            key={index}
                            style={getStyle(style, index, newActiveIndex, element.disabled)}
                        >
                            <button
                                type="button"
                                style={style.tabButton}
                                data-handle={getDataHandle(
                                        element.name,
                                        index,
                                        newActiveIndex,
                                        element.disabled)
                                }
                                disabled={element.disabled}
                                onClick={() => (
                                    !element.disabled ? this.onClick(index) : null
                                )}
                                onMouseDown={() => (
                                       !element.disabled ? this.onClick(index) : null
                                )}
                            >
                                {element.tab}
                            </button>
                        </div>
                    ))}
                </div>
                { tabs[newActiveIndex].page }
            </div>
        );
    }
}

TabDeck.propTypes = {
    defaultIndex: PropTypes.number,
    onChangeTab: PropTypes.func,
    tabs: PropTypes.arrayOf(PropTypes.object),
};

TabDeck.defaultProps = {
    defaultIndex: 0,
    onChangeTab: () => null,
    tabs: [],
};

export default TabDeck;
