import { logout as jsonrpcLogout } from '@webui-one/core/jsonrpc';
import { setAuthenticated } from 'modules/application/actions';
import { isNonEmptyArray } from '@webui-one/core/utils/array';
import { isObject } from '@webui-one/core/utils/object';
import { UNSAFEWindowLocationHref } from '@webui-one/core/utils/unsafe';
import { clearSessionStorage } from '@webui-one/core/utils/browser';
import { LOGOUT_REASON } from '@webui-one/core/utils/session';
import { loginErrorAction } from 'modules/login/actions';
import { tryGetTransChanges } from 'modules/transactions/actions';

import * as actionTypes from './actionTypes';

export const logoutAction = clearStorage => ({ type: actionTypes.LOGOUT, clearStorage });
export const logoutErrorAction = error => ({ type: actionTypes.LOGOUT_ERROR, error });
export const logoutPendingAction = isPendingLogout => ({
    type: actionTypes.LOGOUT_SET_PENDING,
    isPendingLogout,
});

export const logout = (
    clearStorage,
    {
        funcs: {
            clearBrowserSessionStorage = clearSessionStorage,
            logoutFunc = jsonrpcLogout,
            redirectToLocation = UNSAFEWindowLocationHref,
        } = {},
    } = {},
) =>
    (dispatch) => {
        dispatch(setAuthenticated(false));
        return logoutFunc()
            .then(() => {
                clearBrowserSessionStorage();
                dispatch(logoutAction(clearStorage));
                redirectToLocation('/');
            })
            .catch(error => dispatch(logoutErrorAction(error)));
    };

export const tryLogout = (
    clearStorage,
    {
        funcs: {
            logoutThunk = logout,
            tryGet = tryGetTransChanges,
        } = {},
    } = {},
) => dispatch =>
    dispatch(tryGet()).then((action) => {
        if (isObject(action) && isNonEmptyArray(action.changes)) {
            return dispatch(logoutPendingAction(true));
        }
        return dispatch(logoutThunk(clearStorage));
    });

export const readLogoutReason = () => (
    (dispatch) => {
        const reason = window.sessionStorage.getItem(LOGOUT_REASON);
        window.sessionStorage.removeItem(LOGOUT_REASON);
        if (typeof reason === 'string' && reason.trim() !== '') {
            return Promise.resolve(dispatch(loginErrorAction({
                data: {
                    reason,
                },
            })));
        }
        return Promise.resolve();
    }
);

