import React, { useState } from 'react';
import PropTypes from 'prop-types';
import InformationSymbol from '@webui-one/styles/static/InformationSymbol';
import Modal from '../../Modal/Modal';
import InformationTable from './InformationTable';

const style = {
    button: {
        width: '100%',
        height: '100%',
        border: 'none',
        backgroundColor: 'transparent',
        padding: '0',
        lineHeight: '0',
        cursor: 'pointer',
    },
};

const InformationButton = ({
    circleColor,
    iColor,
    path,
}) => {
    const [showModal, setShowModal] = useState(false);
    return (
        <div>
            <button onClick={() => setShowModal(true)} style={style.button} type="button" >
                <InformationSymbol circleColor={circleColor} iColor={iColor} />
            </button>
            {showModal && (
                <Modal onRequestClose={() => setShowModal(false)}>
                    <InformationTable path={path} />
                </Modal>
            )}
        </div>
    );
};

InformationButton.propTypes = {
    circleColor: PropTypes.string,
    iColor: PropTypes.string,
    path: PropTypes.string.isRequired,
};
export default InformationButton;
