import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { selectIsEditMode } from './reducer';
import { setEditModeAction } from './actions';
import ToggleButtonContainer from './ToggleButtonContainer';
import EditModeIcon from './static/EditModeIcon';

class EditModeButtonContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onToggle = this.onToggle.bind(this);
    }

    onToggle() {
        const { isEditMode, onSetEditMode } = this.props;
        onSetEditMode(!isEditMode);
    }

    render() {
        const { isEditMode, shortcut } = this.props;
        return (
            <ToggleButtonContainer
                dataHandle="editModeButton"
                isChecked={isEditMode}
                onToggle={this.onToggle}
                shortcut={shortcut}
                toolTipText="more node options"
            >
                <EditModeIcon />
            </ToggleButtonContainer>
        );
    }
}

EditModeButtonContainer.propTypes = {
    isEditMode: PropTypes.bool,
    onSetEditMode: PropTypes.func,
    shortcut: PropTypes.string,
};

EditModeButtonContainer.defaultProps = {
    shortcut: 'vs',
};

const mapDispatchToProps = dispatch => ({
    onSetEditMode(value) {
        dispatch(setEditModeAction(value));
    },
});

const mapStateToProps = state => (
    { isEditMode: selectIsEditMode(state) }
);

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(EditModeButtonContainer);

export {
    EditModeButtonContainer as _EditModeButtonContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
