import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';

const container = {
    position: 'absolute',
    display: 'flex',
    zIndex: '5',
};

const label = {
    backgroundColor: defaultStyles.color2,
    color: defaultStyles.color3,
    padding: '0.5rem',
    fontSize: defaultStyles.medium1,
    borderRadius: '0.25rem',
    lineHeight: '1.7em',
    whiteSpace: 'nowrap',
    ...defaultStyles.fontLight,
};

const pointyThing = {
    border: 'solid 0.25rem transparent',
    alignSelf: 'center',
};

const styles = {
    up: {
        container: {
            ...container,
            flexDirection: 'column',
            width: 'fit-content',
            left: '50%',
            transform: 'translate(-50%, -100%)',
        },
        label,
        pointyThing: {
            ...pointyThing,
            borderTop: `solid 0.5rem ${defaultStyles.color2}`,
        },
    },
    down: {
        container: {
            ...container,
            left: '50%',
            transform: 'translate(-50%, 0%)',
            flexDirection: 'column-reverse',
        },
        label,
        pointyThing: {
            ...pointyThing,
            borderBottom: `solid 0.5rem ${defaultStyles.color2}`,
        },
    },
    left: {
        container: {
            ...container,
            top: '50%',
            transform: 'translate(-100%, -50%)',
        },
        label,
        pointyThing: {
            ...pointyThing,
            borderLeft: `solid 0.5rem ${defaultStyles.color2}`,
        },
    },
    right: {
        container: {
            ...container,
            top: '50%',
            left: '100%',
            transform: 'translate(0, -50%)',
            flexDirection: 'row-reverse',
        },
        label,
        pointyThing: {
            ...pointyThing,
            borderRight: `solid 0.5rem ${defaultStyles.color2}`,
        },
    },
    lowerLeft: {
        container: {
            ...container,
            top: '50%',
            transform: 'translate(-100%, -50%)',
        },
        label: {
            ...label,
            position: 'absolute',
            top: '-5px',
            right: '12px',
        },
        pointyThing: {
            ...pointyThing,
            borderLeft: `solid 0.5rem ${defaultStyles.color2}`,
        },
    },
};

const shouldWrapText = (style, shouldWrap) => (
    shouldWrap ? {
        ...style,
        label: {
            ...style.label,
            whiteSpace: 'normal',
        },
    } : style
);

const getStyle = (styleName, shouldWrap) => (
    shouldWrapText(styles[styleName], shouldWrap) || shouldWrapText(styles.right, shouldWrap)
);

export {
    getStyle,
    shouldWrapText,
};
