import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Loader from '../../Loading/Loader';
import _useInfos, {
    selectInfos,
    loadingInfos,
} from '../../hooks/useInfos';
import style from './InformationTableStyles';

const isLargeText = ({ title }) =>
    ['Description'].some(h => h === title);

const InformationTable = ({ path, useInfos }) => {
    const state = useInfos({ path });

    const infos = selectInfos(state);

    if (loadingInfos(state)) {
        return (
            <div data-handle="info-loader" style={style.loader}>
                <Loader color={defaultStyles.color6} />
            </div>
        );
    }
    const largeTexts = infos.filter(isLargeText);
    const tableTexts = infos.filter(i => !isLargeText(i));
    return (
        <div data-handle="info-content" style={style.content}>
            <div data-handle="info-table" style={style.info}>
                {tableTexts.map(({ title, content }, ix) => (
                    <Fragment key={`${title}-${ix}`}>
                        <div style={style.header}>{`${title}: `}</div>
                        <div style={style.item}>{content}</div>
                    </Fragment>
                ))}
            </div>
            {largeTexts.length !== 0 && (
                <div data-handle="info-description" style={style.description}>
                    {largeTexts.map(({ title, content }, ix) => (
                        <Fragment key={`${title}-${ix}`}>
                            <div style={style.header}>
                                {`${title}:`}
                            </div>
                            <div style={style.bread}>{content}</div>
                        </Fragment>
                    ))}
                </div>
            )}
        </div>
    );
};

InformationTable.propTypes = {
    path: PropTypes.string,
    useInfos: PropTypes.func,
};

InformationTable.defaultProps = {
    useInfos: _useInfos,
};

export default InformationTable;
