import { useEffect, useReducer, useCallback } from 'react';
import { getLastTh as _getLastTh } from '@webui-one/core/jsonrpc';

const initialState = {
    isExecuting: false,
    asyncState: {
        isLoading: false,
        data: null,
        error: null,
    },
};

const actionTypes = {
    execute: 'execute',
    start: 'start',
    success: 'success',
    error: 'error',
};

function reducer(state, action) {
    switch (action.type) {
    case actionTypes.execute:
        return {
            ...state,
            isExecuting: true,
        };
    case actionTypes.start:
        return {
            ...state,
            isExecuting: true,
            asyncState: {
                ...initialState.asyncState,
                isLoading: true,
            },
        };
    case actionTypes.success:
        return {
            ...state,
            isExecuting: false,
            asyncState: {
                ...state.asyncState,
                isLoading: false,
                data: action.data,
            },
        };
    case actionTypes.error:
        return {
            ...state,
            isExecuting: false,
            asyncState: {
                ...state.asyncState,
                isLoading: false,
                error: action.error,
            },
        };
    default:
        return state;
    }
}

export const useTransactionJsonRpcOnDemand = ({
    asyncFunc,
    processResultFunc,
    funcs: {
        getLastTh = _getLastTh,
    } = {},
} = {}) => {
    const [state, dispatch] = useReducer(reducer, initialState);
    const execute = useCallback(() => {
        dispatch({ type: actionTypes.execute });
    }, []);

    useEffect(() => {
        if (!state.isExecuting) return () => null;
        const controller = new AbortController();
        const signal = controller.signal;

        async function fetch() {
            try {
                dispatch({ type: actionTypes.start });
                const th = await getLastTh({ signal });
                const response = await asyncFunc({ signal, th });

                if (!signal.aborted) {
                    const data = processResultFunc(response.results);
                    dispatch({ type: actionTypes.success, data });
                }
            } catch (error) {
                if (!signal.aborted) {
                    dispatch({ type: actionTypes.error, error });
                }
            }
        }
        fetch();

        return () => {
            if (signal && signal.abort) {
                signal.abort();
            }
        };
    }, [state.isExecuting, dispatch, asyncFunc, processResultFunc, getLastTh]);

    return [state.asyncState, execute];
};

export {
    initialState as _initialState,
    reducer as _reducer,
    actionTypes as _actionTypes,
};
