import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from '@webui-one/router';
import { selectNumberOfChanges } from 'modules/transactions/selectors';
import {
    logout,
    tryLogout,
    logoutPendingAction,
} from 'modules/logout/actions';
import HelpModal from 'Common/Components/HelpModal/HelpModal';
import DevFlagsButtonContainer from 'modules/flags/DevFlagsButtonContainer';
import { formatPath } from '@webui-one/core/utils/path';
import { isDevEnv } from '@webui-one/core/utils/platform';
import { selectIsPendingLogout } from 'modules/logout/reducer';
import { getApplicationName } from 'modules/application/utils';
import { showHelpAction, showSettingsAction } from 'modules/application/actions';
import { selectNsoUser, selectNsoVersion } from 'modules/systemSetting/reducer';
import { selectHelpIsVisible, selectSettingsIsVisible } from 'modules/application/selectors';
import Header from '@webui-one/components/Header/Header';
import HiddenSchemaHandlerModal from 'Common/Components/Modal/HiddenSchemaHandlerModal';
import Settings from 'Common/Components/Settings/Settings';

const HeaderContainer = ({
    getCiscoLogoUrl,
    helpIsVisible,
    onShowHelp,
    onShowSettings,
    settingsIsVisible,
    version,
    ...props
}) => {
    const [showHiddenValueModal, setShowHiddenValueModal] = useState(false);

    const dropdownItems = [
        { name: 'settings', onClick: () => onShowSettings(true) },
        { name: 'help', onClick: () => onShowHelp(true) },
        { name: 'manage hidden groups', onClick: () => setShowHiddenValueModal(!showHiddenValueModal) },
    ];

    const versionText = `NSO VERSION:${version}`;

    return (
        <>
            <Header
                additionalDropdownComponents={isDevEnv() && <DevFlagsButtonContainer />}
                additionalDropdownItems={dropdownItems}
                ciscoLogoUrl={getCiscoLogoUrl()}
                LinkComponent={Link}
                versionText={versionText}
                {...props}
            />
            <HiddenSchemaHandlerModal
                shouldRender={showHiddenValueModal}
                onRequestClose={() => setShowHiddenValueModal(false)}
            />
            {helpIsVisible && <HelpModal onRequestClose={() => onShowHelp(false)} />}
            {settingsIsVisible && <Settings onRequestClose={() => onShowSettings(false)} /> }
        </>
    );
};

HeaderContainer.propTypes = {
    version: PropTypes.string,
    getCiscoLogoUrl: PropTypes.func,
    helpIsVisible: PropTypes.bool,
    onShowHelp: PropTypes.func,
    onShowSettings: PropTypes.func,
    settingsIsVisible: PropTypes.bool,
};

HeaderContainer.defaultProps = {
    getCiscoLogoUrl: formatPath,
};

const mapDispatchToProps = dispatch => ({
    onLogoutRequest() {
        dispatch(tryLogout());
    },
    onLogoutConfirm() {
        dispatch(logout());
    },
    onLogoutCancel() {
        dispatch(logoutPendingAction(false));
    },
    onShowHelp(value) {
        dispatch(showHelpAction(value));
    },
    onShowSettings(value) {
        dispatch(showSettingsAction(value));
    },
});

const mapStateToProps = (state, ownProps) => ({
    applicationName: getApplicationName(state, ownProps),
    numberOfChanges: selectNumberOfChanges(state),
    isPendingLogout: selectIsPendingLogout(state),
    helpIsVisible: selectHelpIsVisible(state),
    settingsIsVisible: selectSettingsIsVisible(state),
    user: selectNsoUser(state),
    version: selectNsoVersion(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(HeaderContainer);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    HeaderContainer as _HeaderContainer,
};
