import React from 'react';
import PropTypes from 'prop-types';
import ciscoLogo from '@webui-one/styles/static/cisco.svg';
import Link from '../Link';
import Dropdown from '../Dropdown/DropdownContainer';
import ModalConfirm from '../Modal/ModalConfirm';
import modalStyles from '../Modal/ModalStyles';
import style from './HeaderStyles';


const getAdditionalElements = (comps) => {
    const components = comps || [];
    return components.map((component, key) => (
        <div style={style.headerElement} key={key}>
            {component}
        </div>
    ));
};

const Header = ({
    additionalComponents,
    additionalComponentsRight,
    additionalDropdownComponents,
    additionalDropdownItems,
    applicationName,
    children,
    ciscoLogoUrl,
    dropdownStyleName,
    isPendingLogout,
    LinkComponent,
    numberOfChanges,
    onLogoutCancel,
    onLogoutConfirm,
    onLogoutRequest,
    user,
    versionText,
}) => {
    const dropdownItems = [
        ...additionalDropdownItems,
        { name: 'log out', onClick: onLogoutRequest },
    ];

    const pathCiscoLogo = user ? ciscoLogo : `/login${ciscoLogo}`;
    return (
        <div style={style.header}>
            <div style={style.headerBox} >
                <LinkComponent to={ciscoLogoUrl} style={style.logo}>
                    <img src={pathCiscoLogo} style={style.logoImage} alt="Cisco" />
                </LinkComponent>
                <div style={style.title} data-handle="headerTitle">
                    <div style={style.appName}>{applicationName}</div>
                    <div style={style.version}>{versionText}</div>
                </div>
                {getAdditionalElements(additionalComponents)}
                <div style={style.floatRight}>
                    {getAdditionalElements(additionalComponentsRight)}
                    {<div data-handle="headerUser" style={style.user}>
                        <ModalConfirm
                            shouldRender={isPendingLogout}
                            onConfirm={onLogoutConfirm}
                            formIsValid
                            onRequestClose={onLogoutCancel}
                        >
                            <div style={modalStyles.header}>
                                Sure you want to logout?
                            </div>
                            <div style={modalStyles.bread}>
                                {`${numberOfChanges} changes will be lost?`}
                            </div>
                        </ModalConfirm>
                        <Dropdown
                            items={dropdownItems}
                            styleName={dropdownStyleName}
                            title={user || ''}
                        >
                            {additionalDropdownComponents}
                        </Dropdown>
                    </div>
                    }
                </div>
            </div>
            {children}
        </div>
    );
};

Header.propTypes = {
    additionalComponents: PropTypes.arrayOf(PropTypes.element),
    additionalComponentsRight: PropTypes.arrayOf(PropTypes.element),
    additionalDropdownItems: PropTypes.arrayOf(PropTypes.shape({
        onClick: PropTypes.func,
        name: PropTypes.name,
    })).isRequired,
    additionalDropdownComponents: PropTypes.oneOfType([
        PropTypes.element,
        PropTypes.bool,
    ]),
    applicationName: PropTypes.string,
    children: PropTypes.node,
    dropdownStyleName: PropTypes.string,
    ciscoLogoUrl: PropTypes.string,
    isPendingLogout: PropTypes.bool,
    LinkComponent: PropTypes.elementType,
    numberOfChanges: PropTypes.number,
    onLogoutCancel: PropTypes.func,
    onLogoutConfirm: PropTypes.func,
    onLogoutRequest: PropTypes.func,
    user: PropTypes.string,
    versionText: PropTypes.string.isRequired,
};

Header.defaultProps = {
    additionalDropdownItems: [],
    isPendingLogout: false,
    LinkComponent: Link,
    versionText: '',
};

export default Header;
