import React, { useState, useCallback } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
    useTransactionJsonRpcOnDemand,
} from '@webui-one/components/hooks/useTransactionJsonRpcOnDemand';
import Loader from '@webui-one/components/Loading/Loader';
import Modal from '@webui-one/components/Modal/Modal';
import modalStyle from '@webui-one/components/Modal/ModalStyles';
import { unhideSchema, hideSchema } from '@webui-one/core/jsonrpc';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { reEvaluateSchemaThunk } from 'modules/schemas/thunks';

const inputContainerStyle = {
    minWidth: '15rem',
    minHeight: '1.4rem',
    position: 'relative',
    whiteSpace: 'nowrap',
    margin: '1rem 0px',
};

const labelStyle = {
    marginLeft: '0.2rem',
    marginRight: '0.2rem',
    display: 'inline-block',
    width: '6em',
};

const footerStyles = {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    fontSize: '0.9rem',
    marginTop: '1rem',
};

const errorStyles = {
    color: defaultStyles.color5,
};

const HIDE_OR_SHOW_VALUES = {
    HIDE: 'Hide',
    SHOW: 'Show',
};

function HiddenSchemaHandlerModal({ shouldRender, onRequestClose, onReevaluateSchema }) {
    const [groupNameValue, setGroupNameValue] = useState('');
    const [passwordValue, setPasswordValue] = useState('');
    const [hideOrShowValue, setHideOrShowValue] = useState(HIDE_OR_SHOW_VALUES.SHOW);

    const [state, call] = useTransactionJsonRpcOnDemand({
        asyncFunc: useCallback(({ signal, th }) => {
            if (hideOrShowValue === HIDE_OR_SHOW_VALUES.SHOW) {
                const unhideResult = unhideSchema({
                    passwd: passwordValue,
                    groupName: groupNameValue,
                    signal,
                    th,
                });
                onReevaluateSchema();
                return unhideResult;
            }
            const hideResult = hideSchema({
                groupName: groupNameValue,
                signal,
                th,
            });
            onReevaluateSchema();
            return hideResult;
        }, [groupNameValue, passwordValue, hideOrShowValue, onReevaluateSchema]),

        processResultFunc: useCallback(() => 'success', []),
    });

    const handleSubmit = () => {
        if (groupNameValue) {
            call();
        }
    };

    const handleOnRequestClose = () => {
        setGroupNameValue('');
        setPasswordValue('');
        onRequestClose();
    };

    const formIsValid = groupNameValue !== '';
    let errorMsg = state.error ? state.error.message : '';
    if (state.error && state.error.data && state.error.data.param) {
        errorMsg += ` ${state.error.data.param}`;
    }
    if (state.error && state.error.data && state.error.data.reason) {
        errorMsg += ` ${state.error.data.reason}`;
    }

    let buttonStyle = formIsValid ? defaultStyles.button : defaultStyles.buttonDisabled;
    buttonStyle = { ...buttonStyle, marginRight: '1rem' };
    return (
        <Modal
            shouldRender={shouldRender}
            title="Manage hidden groups"
            onRequestClose={handleOnRequestClose}
            onConfirm={handleSubmit}
            formIsValid={formIsValid}
            closeOnConfirm={false}
        >
            <form onSubmit={handleSubmit}>
                <p style={{ width: modalStyle.modalBox.minWidth }}>
                    A hide group can be unhidden only if this has been explicitly
                    allowed in the ncs.conf configuration.
                </p>

                <div style={inputContainerStyle}>
                    <div style={labelStyle}>Group name</div>
                    <input
                        onChange={e => setGroupNameValue(e.target.value)}
                    />
                </div>

                <div style={inputContainerStyle}>
                    <div style={labelStyle}>Password</div>
                    <input
                        onChange={e => setPasswordValue(e.target.value)}
                    />
                </div>

                <button
                    type="button"
                    data-handle="hide-key"
                    key="Hide-key"
                    disabled={!formIsValid}
                    style={buttonStyle}
                    onClick={() => { setHideOrShowValue('Hide'); handleSubmit(); }}
                >
                    Hide
                </button>

                <button
                    type="button"
                    data-handle="show-key"
                    key="Show-key"
                    disabled={!formIsValid}
                    style={buttonStyle}
                    onClick={() => { setHideOrShowValue('Show'); handleSubmit(); }}
                >
                    Show
                </button>

                <button
                    type="button"
                    data-handle="cancel-key"
                    key="Cancel-key"
                    disabled={false}
                    style={{ ...defaultStyles.button, marginRight: '1rem' }}
                    onClick={handleOnRequestClose}
                >
                    Cancel
                </button>

                {(state.data || state.error) && (
                    <div style={footerStyles}>
                        <div style={{ width: modalStyle.modalBox.minWidth }}>
                            { state.data && <span>Ok</span> }
                            { state.error && <span style={errorStyles}>{errorMsg}</span> }
                        </div>
                    </div>
                )}

                <div>
                    { state.isLoading && <Loader color={defaultStyles.color9} />}
                </div>
            </form>
        </Modal>
    );
}

HiddenSchemaHandlerModal.propTypes = {
    shouldRender: PropTypes.bool,
    onRequestClose: PropTypes.func,
    onReevaluateSchema: PropTypes.func,
};

const mapDispatchToProps = dispatch => ({
    onReevaluateSchema() {
        dispatch(reEvaluateSchemaThunk([], true));
    },
});

export default connect(
    undefined,
    mapDispatchToProps,
)(HiddenSchemaHandlerModal);
