import { connect } from 'react-redux';
import FlagsButton from '@webui-one/components/FlagsButton/FlagsButton';
import { getAvailableFlags } from 'modules/flags/flags';
import { selectFlags } from './selectors';

import {
    setFlags,
} from './actions';

const mapStateToProps = state => ({
    availableFlags: getAvailableFlags(),
    flags: selectFlags(state),
});

const mapDispatchToProps = dispatch => ({
    onSetFlags(flags) {
        dispatch(setFlags(flags));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(FlagsButton);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
