import React from 'react';
import PropTypes from 'prop-types';
import style from './InputStyles';

class Input extends React.Component {
    constructor(props) {
        super(props);
        this.handleBlur = this.handleBlur.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleFocus = this.handleFocus.bind(this);
        this.handleKeyDown = this.handleKeyDown.bind(this);
        this.handleSetValue = this.handleSetValue.bind(this);
        this.inputStyle = this.inputStyle.bind(this);

        this.state = {
            draftValue: props.value,
            hasFocus: false,
            isModified: false,
            usePasswordType: false,
        };
    }

    static getDerivedStateFromProps(props, state) {
        const { value, isLoading } = props;
        const { hasFocus, draftValue } = state;
        if (!hasFocus && !isLoading && value !== draftValue) {
            return { draftValue: value };
        }
        return null;
    }

    shouldComponentUpdate(nextProps, nextState) {
        const {
            editable: nextEditable,
            error: nextError,
            isLoading: nextIsLoading,
            suppressEcho: nextSuppressEcho,
        } = nextProps;
        const { value } = nextState;
        const {
            editable,
            error,
            isLoading,
            suppressEcho,
        } = this.props;
        const {
            draftValue,
        } = this.state;

        if (nextEditable !== editable) return true;
        if (nextError !== error) return true;
        if (nextIsLoading !== isLoading) return true;
        if (nextSuppressEcho !== suppressEcho) return true;
        if (value !== draftValue) return true;
        return false;
    }

    handleFocus() {
        const { suppressEcho } = this.props;
        this.setState({
            hasFocus: true,
            isModified: false,
            usePasswordType: suppressEcho,
        });
    }

    handleChange(e) {
        const { onChange } = this.props;
        onChange(e.target.value);
        this.setState({ draftValue: e.target.value, isModified: true });
    }

    handleSetValue(force) {
        const { onSetValue, value } = this.props;
        const { draftValue, isModified } = this.state;

        if (draftValue !== value || isModified || force) {
            onSetValue(draftValue);
        }
    }

    handleBlur() {
        this.handleSetValue();
        this.setState({ hasFocus: false });
    }

    handleKeyDown(e) {
        const { value } = this.props;
        e.persist();
        if (e.key === 'Enter') {
            this.handleSetValue(true);
        }

        if (e.key === 'Escape') {
            this.setState({
                draftValue: value,
                isModified: false,
            }, () => {
                e.target.blur();
            });
        }
    }

    inputStyle() {
        const { editable, error } = this.props;
        if (!editable) {
            return style.inputFieldReadOnly;
        }
        if (error) return style.inputFieldError;
        return style.inputField;
    }

    render() {
        const {
            name,
            editable,
            placeholder,
            suppressEcho,
            isDefaultValue,
            autoFocus } = this.props;
        const { draftValue, hasFocus, usePasswordType } = this.state;
        const draftValueDefault = (isDefaultValue && !hasFocus) ? `(${draftValue})` : draftValue;
        const inputStyle = this.inputStyle();
        const inputType = usePasswordType ? 'password' : 'text';

        return (
            <input
                autoFocus={autoFocus}
                data-handle={name}
                autoComplete={suppressEcho ? 'new-password' : 'on'}
                style={inputStyle}
                readOnly={!editable}
                placeholder={placeholder}
                type={inputType}
                value={draftValueDefault}
                onBlur={this.handleBlur}
                onFocus={this.handleFocus}
                onChange={this.handleChange}
                onKeyDown={this.handleKeyDown}
            />
        );
    }
}

Input.propTypes = {
    autoFocus: PropTypes.bool,
    editable: PropTypes.bool,
    error: PropTypes.shape(Object),
    isDefaultValue: PropTypes.bool,
    isLoading: PropTypes.bool,
    name: PropTypes.string,
    onChange: PropTypes.func,
    onSetValue: PropTypes.func,
    placeholder: PropTypes.string,
    suppressEcho: PropTypes.bool,
    value: PropTypes.string,
};

Input.defaultProps = {
    value: '',
    suppressEcho: false,
    isDirty: false,
    onChange: () => (null),
    onSetValue: () => (null),
};

export default Input;
