import { useRef } from 'react';
import PropTypes from 'prop-types';

function useSafeOnClick(func) {
    const ref = useRef(false);

    async function safeOnClick() {
        if (!ref.current) {
            ref.current = true;
            try {
                await func();
            } finally {
                ref.current = false;
            }
        }
    }

    return safeOnClick;
}

useSafeOnClick.propTypes = {
    func: PropTypes.func.isRequired,
};

export default useSafeOnClick;
