import React from 'react';
import PropTypes from 'prop-types';
import getStyle, { noInputAppearance } from './CheckBoxStyles';

class Checkbox extends React.Component {
    constructor(props) {
        super(props);
        this.handleToggleCheckbox = this.handleToggleCheckbox.bind(this);
        this.checkboxStyle = this.checkboxStyle.bind(this);
        this.state = {};
    }

    handleToggleCheckbox() {
        const {
            onSetValue,
            value,
        } = this.props;

        onSetValue(!value);
    }

    handleFocus(focusState) {
        this.setState({ isFocused: focusState });
    }

    checkboxStyle() {
        const { editable, value, styleName } = this.props;
        const { isFocused } = this.state;
        const style = getStyle(styleName);

        if (!editable) {
            return value ? style.checkbox.noeditChecked : style.checkbox.noedit;
        }

        let editableStyle = value ? style.checkbox.checked : style.checkbox.unchecked;
        if (isFocused) editableStyle = { ...editableStyle, boxShadow: '0 0 5px blue' };
        return editableStyle;
    }


    render() {
        const { editable, value, styleName, name, children } = this.props;
        const style = getStyle(styleName);
        const checked = !!value;

        return (
            // eslint-disable-next-line jsx-a11y/label-has-associated-control
            <label
                data-handle={name}
                style={style.label}
            >
                <input
                    disabled={!editable}
                    onChange={editable ? this.handleToggleCheckbox : null}
                    type="checkbox"
                    checked={checked}
                    style={noInputAppearance}
                    onFocus={() => this.handleFocus(true)}
                    onBlur={() => this.handleFocus(false)}
                />
                <div style={this.checkboxStyle()}>
                    {value && <div data-handle="checkboxArrow" style={style.arrow} />}
                </div>
                {children}
            </label>
        );
    }
}

Checkbox.propTypes = {
    children: PropTypes.shape(Object),
    editable: PropTypes.bool,
    name: PropTypes.string,
    onSetValue: PropTypes.func,
    styleName: PropTypes.string,
    value: PropTypes.bool,
};

Checkbox.defaultProps = {
    editable: true,
};

export default Checkbox;
