import React from 'react';
import PropTypes from 'prop-types';
import { getStyle } from './ToolTipStyles';

export default class ToolTip extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showTooltip: false,
        };
    }

    componentWillUnmount() {
        clearTimeout(this.toolTipTimer);
    }

    show() {
        const { timeout } = this.props;
        clearTimeout(this.toolTipTimer);
        this.toolTipTimer = setTimeout(
            () => {
                this.setState({ showTooltip: true });
            }, timeout);
    }

    hide() {
        clearTimeout(this.toolTipTimer);
        this.setState({ showTooltip: false });
    }

    render() {
        const { showTooltip } = this.state;
        if (!showTooltip) return null;
        const { additionalTexts, toolTipText, direction, shouldWrap, minWidth } = this.props;
        if (!toolTipText && !additionalTexts) return null;

        const stl = getStyle(direction, shouldWrap);

        let content;
        if (additionalTexts && additionalTexts.length > 0) {
            const multilineStyle = {
                ...stl.label,
                whiteSpace: 'pre-wrap',
            };
            const text = [
                toolTipText,
                ...additionalTexts,
            ].filter(Boolean).join('\n');

            content = <pre style={multilineStyle}>{text}</pre>;
        } else {
            content = <p style={stl.label}>{toolTipText}</p>;
        }

        return (
            <div>
                <div data-handle="toolTip" style={{ ...stl.container, minWidth }}>
                    { content }
                    <div style={stl.pointyThing} />
                </div>
            </div>
        );
    }
}

ToolTip.propTypes = {
    toolTipText: PropTypes.string,
    additionalTexts: PropTypes.arrayOf(PropTypes.string),
    direction: PropTypes.string,
    timeout: PropTypes.number,
    shouldWrap: PropTypes.bool,
    minWidth: PropTypes.string,
};

ToolTip.defaultProps = {
    timeout: 500,
    minWidth: '0',
};
