import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';

const checkbox = {
    width: '1rem',
    height: '1rem',
    borderWidth: '1px',
    borderColor: defaultStyles.color1,
    borderStyle: 'solid',
    cursor: 'pointer',
    alignSelf: 'center',
    display: 'flex',
    flexShrink: '0',
    boxSizing: 'border-box',
    justifyContent: 'center',
};

const arrow = {
    width: '0.25rem',
    marginTop: '0.1rem',
    borderColor: defaultStyles.color3,
    borderStyle: 'solid',
    borderBottomWidth: '2px',
    borderRightWidth: '2px',
    borderTopWidth: '0px',
    borderLeftWidth: '0px',
    transform: 'rotate(45deg)',
    height: '0.5rem',
};

const label = {
    display: 'flex',
    cursor: 'pointer',
};

const checked = {
    borderStyle: 'none',
    backgroundColor: defaultStyles.color1,
};

const defaultStyle = {
    checkbox: {
        unchecked: {
            ...checkbox,
        },
        checked: {
            ...checkbox,
            ...checked,
        },
        noedit: {
            ...checkbox,
            borderColor: defaultStyles.color6,
        },
        noeditChecked: {
            ...checkbox,
            borderStyle: 'none',
            backgroundColor: defaultStyles.color6,
        },
    },
    arrow: {
        ...arrow,
    },
    label,
    labelDisabled: {
        ...label,
        color: defaultStyles.color7,
    },
};

const round = {
    ...checkbox,
    borderRadius: '0.5rem',
};

const dot = {
    backgroundColor: defaultStyles.color1,
    width: '0.5rem',
    height: '0.5rem',
    borderRadius: '.25rem',
    margin: '0.2rem',
};

const radio = {
    checkbox: {
        unchecked: {
            ...round,
            borderColor: defaultStyles.color1,
        },
        checked: {
            ...round,
            borderColor: defaultStyles.color1,
        },
        noedit: {
            ...round,
            borderColor: defaultStyles.color6,
        },
    },
    arrow: {
        ...dot,
    },
    label: {
        ...label,
        padding: '0.25rem',
    },
};

const padded = {
    checkbox: {
        unchecked: {
            ...checkbox,
        },
        checked: {
            ...checkbox,
            ...checked,
            borderStyle: 'none',
        },
        noedit: {
            ...checkbox,
            borderColor: defaultStyles.color6,
        },
        noeditChecked: {
            ...checkbox,
            backgroundColor: defaultStyles.color6,
            borderStyle: 'none',
        },
    },
    arrow: {
        ...arrow,
    },
    label: {
        ...label,
        padding: '0.25rem',
    },
    labelDisabled: {
        ...label,
        padding: '0.25rem',
    },
};

const grey = {
    checkbox: {
        unchecked: {
            ...checkbox,
            borderColor: defaultStyles.color2,
        },
        checked: {
            ...checkbox,
            borderColor: defaultStyles.color2,
        },
    },
    arrow: {
        ...arrow,
        borderColor: defaultStyles.color2,
    },
    label,
    labelDisabled: {
        ...label,
        color: defaultStyles.color7,
    },
};

export const noInputAppearance = {
    appearance: 'none',
    MozAppearance: 'none',
    WebkitAppearance: 'none',
    margin: '0',
};

const styles = {
    defaultStyle,
    grey,
    padded,
    radio,
};

export default styleName => (styles[styleName] || styles.defaultStyle);
