import {
    useCallback,
    useEffect,
    useReducer,
} from 'react';

import { getOrNewReadWriteTh } from '@webui-one/core/jsonrpc';
import { lstrip } from '@webui-one/core/utils/string';
import {
    dataOrDefault,
    loadApplications,
    saveApplication,
} from './actions';
import { DEFAULT_SETTINGS } from './constants';

const actionTypes = {
    GET_DATA: 'GET_DATA',
    GET_DATA_SUCCESS: 'GET_DATA_SUCCESS',
    GET_DATA_ERROR: 'GET_DATA_ERROR',
    ASYNC_PENDING: 'ASYNC_PENDING',
    ASYNC_SUCCESS: 'ASYNC_SUCCESS',
    ASYNC_ERROR: 'ASYNC_ERROR',
};

const reducer = (state, action) => {
    switch (action.type) {
    case actionTypes.GET_DATA_PENDING:
        return {
            ...state,
            error: undefined,
            isLoading: true,
        };
    case actionTypes.GET_DATA_SUCCESS:
        return {
            ...state,
            data: action.data,
            error: undefined,
            isLoading: false,
        };
    case actionTypes.GET_DATA_ERROR:
        return {
            ...state,
            error: action.error,
            isLoading: false,
        };
    case actionTypes.ASYNC_PENDING: {
        if (state.asyncAction) return state;
        return {
            ...state,
            asyncAction: action,
            isLoading: true,
        };
    }
    case actionTypes.ASYNC_SUCCESS:
        return {
            ...state,
            asyncAction: undefined,
            error: undefined,
            isLoading: false,
        };
    case actionTypes.ASYNC_ERROR:
        return {
            ...state,
            isLoading: false,
            error: action.error,
            asyncAction: undefined,
        };

    default:
        return state;
    }
};

const getAppData = (appId, applications) => {
    const defApp = DEFAULT_SETTINGS.find(({ id }) => id === appId);
    defApp.href = lstrip(defApp.href, '/webui-one/');
    const cdbApp = applications.find(({ id }) => id === appId);
    const newApp = { ...(cdbApp || defApp) };
    return { newApp, defApp };
};

export default function useLoadSettings({ onDone }) {
    const [state, dispatch] = useReducer(reducer, {});
    const { data, asyncAction, error: stateError } = state;
    const applications = dataOrDefault(data, ['applications'], []);
    const dispatchAsync = useCallback(args =>
        dispatch({
            type: actionTypes.ASYNC_PENDING,
            args,
        }),
    []);

    const setHiddenAppHub = (appId, value) => {
        const { newApp } = getAppData(appId, applications);
        newApp.hidden = !value;
        dispatchAsync({ data: [newApp] });
    };

    const setShortcut = (appId, value) => {
        const { newApp, defApp } = getAppData(appId, applications);

        if (value) {
            newApp.shortcut = defApp.shortcut;
            newApp.abbreviation = defApp.abbreviation;
        } else {
            delete newApp.shortcut;
            delete newApp.abbreviation;
        }
        dispatchAsync({ data: [newApp] });
    };
    const setDefault = () => {
        dispatchAsync({ data: DEFAULT_SETTINGS, isDefault: true });
    };
    useEffect(() => {
        if (asyncAction || stateError) return () => null;
        let isCancelled = false;
        const doAsync = async () => {
            dispatch({
                type: actionTypes.GET_DATA_PENDING,
            });
            try {
                const th = await getOrNewReadWriteTh();
                const response = await loadApplications({ th });
                if (isCancelled) return;
                dispatch({
                    type: actionTypes.GET_DATA_SUCCESS,
                    data: response,
                });
                if (onDone) onDone();
            } catch (error) {
                if (isCancelled) return;
                dispatch({
                    type: actionTypes.GET_DATA_ERROR,
                    error,
                });
            }
        };
        doAsync();

        return () => {
            isCancelled = true;
        };
    }, [asyncAction, stateError, onDone]);

    useEffect(() => {
        if (!asyncAction) return () => null;
        let isCancelled = false;
        const doAsync = async () => {
            try {
                const th = await getOrNewReadWriteTh();
                const response = await saveApplication({ th, ...asyncAction.args });
                if (isCancelled) return;
                dispatch({
                    type: actionTypes.ASYNC_SUCCESS,
                    data: response,
                });
            } catch (error) {
                if (isCancelled) return;
                dispatch({
                    type: actionTypes.ASYNC_ERROR,
                    error,
                });
            }
        };
        doAsync();
        return () => {
            isCancelled = true;
        };
    }, [asyncAction]);

    return {
        ...state,
        applications,
        setHiddenAppHub,
        setShortcut,
        setDefault,
    };
}
