import React from 'react';
import PropTypes from 'prop-types';
import Link from '../Link';
import { getPathname } from '@webui-one/core/utils/browser';
import { isNonEmptyString, rstrip } from '@webui-one/core/utils/string';
import style from './FooterStyles';

const formatPathname = pathname => `${rstrip(pathname, '/')}/`;
export function getActiveApplication(applications) {
    const location = formatPathname(getPathname());
    return Object.values(applications).reduce((activeApp, app) => {
        const appHref = formatPathname(app.href);
        if (!location.startsWith(appHref)) {
            return activeApp;
        }
        if (!activeApp) {
            return app;
        }
        const activeHref = formatPathname(activeApp.href);
        if (location === activeHref) {
            return activeApp;
        }
        if (appHref.startsWith(activeHref)) {
            return app;
        }
        return activeApp;
    }, null);
}

class Footer extends React.Component {
    getLink(app, isActive) {
        const { hasChanges, LinkComponent } = this.props;
        const isNative = this.isNativeApplication(app);
        const abbreviation = (
            <div style={style.abbreviation}>
                {app.id === 'Commit manager' && hasChanges
                    ? `${app.abbreviation}*`
                    : app.abbreviation
                }
            </div>
        );
        const name = <div style={style.name}>{app.title}</div>;
        if (!isNonEmptyString(app.href)) {
            return null;
        }
        const linkStyle = isActive
            ? style.shortcutItemActive
            : style.shortcutItem;
        return isNative
            ? (
                <LinkComponent
                    data-handle={`${app.title}Shortcut`}
                    key={app.title}
                    to={app.href}
                    style={linkStyle}
                >
                    {abbreviation}
                    {name}
                </LinkComponent>
            )
            : (
                <a
                    data-handle={`${app.title}Shortcut`}
                    key={app.title}
                    href={app.href}
                    style={linkStyle}
                >
                    {abbreviation}
                    {name}
                </a>
            );
    }

    isNativeApplication(app) {
        const { isNativeApplication } = this.props;
        if (typeof isNativeApplication === 'function') {
            return isNativeApplication(app);
        }
        return true;
    }

    render() {
        const {
            applications,
            onToggleMenu,
            shortcutPanelIsVisible,
        } = this.props;
        const activeApplication = getActiveApplication(applications);
        return (
            <div data-handle="footer" style={style.footer}>
                <button
                    type="button"
                    style={shortcutPanelIsVisible ? style.buttonSlim : style.buttonFat}
                    onClick={onToggleMenu}
                    key="1"
                    data-handle={
                        `showShortcutsButton${shortcutPanelIsVisible ? 'Disabled' : ''}`
                    }
                    disabled={shortcutPanelIsVisible}
                >
                    <div
                        style={style.arrow}
                    >
                        
                    </div>
                </button>
                <div style={shortcutPanelIsVisible ? style.containerUp : style.containerDown}>
                    <button
                        type="button"
                        style={style.spacer}
                        onClick={onToggleMenu}
                        key="2"
                        disabled={!shortcutPanelIsVisible}
                    />
                    {Object.values(applications).map((app) => {
                      if (app.shortcut) return this.getLink(app, activeApplication === app);
                      return null;
                    })}
                    <button
                        type="button"
                        style={style.spacer}
                        onClick={onToggleMenu}
                        key="3"
                        disabled={!shortcutPanelIsVisible}
                        data-handle={
                            `hideShortcutsButton${shortcutPanelIsVisible ? '' : 'Disabled'}`
                        }
                    >
                        <div style={style.arrowDown} ></div>
                    </button>
                </div>
            </div>
        );
    }
}

Footer.propTypes = {
    applications: PropTypes.shape({}),
    hasChanges: PropTypes.bool,
    isNativeApplication: PropTypes.func,
    LinkComponent: PropTypes.elementType,
    onToggleMenu: PropTypes.func,
    shortcutPanelIsVisible: PropTypes.bool,
};

Footer.defaultProps = {
    LinkComponent: Link,
};

export default Footer;

export {
    Footer as _Footer,
};
