import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link, withRouter } from '@webui-one/router';
import defaultApplications, { isNativeApplication } from '@webui-one/core/defaultApplications';
import { transactionIsEmpty } from 'modules/transactions/selectors';
import { applicationsSelector } from 'modules/applications/selectors';
import { UNSAFEWindowLocationHref } from '@webui-one/core/utils/unsafe';
import {
    selectShortcutPanelIsVisible,
    selectLastLocation,
} from 'modules/application/selectors';
import { setShowShortcutPanel } from 'modules/application/actions';
import { getTransChangesAsyncAction } from 'modules/transactions/actions';
import { debounceFunction } from '@webui-one/core/utils/async';
import {
    getApplicationsAsyncAction,
} from 'modules/applications/actions';
import {
    registerShortcut,
    unregisterShortcut,
} from 'modules/shortcuts/actions';
import { getApplicationKey, createPathFromLocation } from '@webui-one/core/utils/path';
import {
    subscribeChangesAction,
    unsubscribeChangesAction,
} from 'modules/comet/actions';
import Footer from '@webui-one/components/Footer/Footer';

const appPath = '/webui:webui/webui-one:applications/application';
const isAppPath = actionPath => (
    actionPath === appPath
    || actionPath.startsWith(`${appPath}{`)
);
const filterApplications = (applications, lastLocation) => {
    const applicationKey = getApplicationKey(window.location.pathname);
    return Object.keys(applications).reduce((acc, name) => {
        const app = applications[name];
        const appKey = getApplicationKey(app.href);
        if (app.id in defaultApplications
            && appKey !== applicationKey
            && lastLocation
            && lastLocation[appKey]) {
            acc[name] = {
                ...app,
                href: lastLocation[appKey],
            };
        } else {
            acc[name] = app;
        }
        return acc;
    }, {});
};

class FooterContainer extends React.Component {
    constructor(props) {
        super(props);
        this.keyMatches = '';
        this.onCometUpdate = this.onCometUpdate.bind(this);
        this.shortcutHandler = this.shortcutHandler.bind(this);
        this.onToggleMenu = this.onToggleMenu.bind(this);
        this.state = {
            shortcutApplications: filterApplications(
                props.applications,
                props.lastLocation,
            ),
        };
        this.callGetApplications = debounceFunction({
            milliseconds: 500,
            waitWhileSpammed: true,
        });
    }

    componentDidMount() {
        const { applications, onLoad, onRegisterShortcut, onSubscribe } = this.props;
        onLoad();
        onRegisterShortcut(
            Object.values(applications).map(app => app.shortcut),
            this.shortcutHandler,
        );
        onSubscribe(appPath, this.onCometUpdate);
    }

    componentDidUpdate(prevProps) {
        const {
            applications,
            lastLocation,
            onRegisterShortcut,
            onUnregisterShortcut,
        } = this.props;

        const {
            applications: prevApplications,
            lastLocation: prevLastLocation,
        } = prevProps;
        if (applications !== prevApplications || lastLocation !== prevLastLocation
        ) {
            // eslint-disable-next-line react/no-did-update-set-state
            this.setState({
                shortcutApplications: filterApplications(
                    applications,
                    lastLocation,
                ),
            });
            if (applications !== prevApplications) {
                onUnregisterShortcut(
                    Object.values(prevApplications).map(app => app.shortcut),
                    this.shortcutHandler,
                );
                onRegisterShortcut(
                    Object.values(applications).map(app => app.shortcut),
                    this.shortcutHandler,
                );
            }
        }
    }

    componentWillUnmount() {
        const { applications, onUnregisterShortcut, onUnsubscribe } = this.props;
        onUnregisterShortcut(
            Object.values(applications).map(app => app.shortcut),
            this.shortcutHandler,
        );
        onUnsubscribe(appPath, this.onCometUpdate);
    }

    onCometUpdate(notification) {
        const { onGetApplications } = this.props;
        const operations = ['created', 'deleted', 'value_set'];
        const shouldUpdateApplications = notification.changes
            .some(({ op, keypath }) => (
                operations.includes(op) &&
                    isAppPath(keypath)
            ), false);
        if (shouldUpdateApplications) {
            this.callGetApplications(onGetApplications);
        }
    }
    onToggleMenu() {
        const { onSetShortcutPanel, shortcutPanelIsVisible } = this.props;
        onSetShortcutPanel(!shortcutPanelIsVisible);
    }

    shortcutHandler(s) {
        const { router } = this.props;
        const { shortcutApplications } = this.state;

        Object.values(shortcutApplications).some(({ href, shortcut }) => {
            const path = createPathFromLocation(router.location);

            if (s === shortcut && href !== path) {
                if (isNativeApplication({ href })) {
                    router.push(href);
                } else {
                    UNSAFEWindowLocationHref(href);
                }
            }
            return s === shortcut;
        });
    }

    render() {
        const {
            hasChanges,
            shortcutPanelIsVisible,
        } = this.props;
        const { shortcutApplications } = this.state;
        return (
            <Footer
                applications={shortcutApplications}
                hasChanges={hasChanges}
                isNativeApplication={isNativeApplication}
                LinkComponent={Link}
                onToggleMenu={this.onToggleMenu}
                shortcutPanelIsVisible={shortcutPanelIsVisible}
            />
        );
    }
}

FooterContainer.propTypes = {
    applications: PropTypes.shape({}),
    hasChanges: PropTypes.bool,
    lastLocation: PropTypes.objectOf(PropTypes.string),
    onGetApplications: PropTypes.func,
    onLoad: PropTypes.func,
    onRegisterShortcut: PropTypes.func,
    onSetShortcutPanel: PropTypes.func,
    onSubscribe: PropTypes.func,
    onUnsubscribe: PropTypes.func,
    onUnregisterShortcut: PropTypes.func,
    router: PropTypes.shape({
        push: PropTypes.func,
        location: PropTypes.shape({}),
    }),
    shortcutPanelIsVisible: PropTypes.bool,
};

FooterContainer.defaultProps = {
    changes: undefined,
};
const mapDispatchToProps = dispatch => ({
    onGetApplications() {
        dispatch(getApplicationsAsyncAction());
    },
    onLoad() {
        return dispatch(getTransChangesAsyncAction());
    },
    onRegisterShortcut(shortcuts, handler) {
        dispatch(registerShortcut(shortcuts, handler));
    },
    onSetShortcutPanel(value) {
        return dispatch(setShowShortcutPanel(value));
    },
    onSubscribe(paths, handler) {
        dispatch(subscribeChangesAction(paths, handler));
    },
    onUnregisterShortcut(shortcuts, handler) {
        dispatch(unregisterShortcut(shortcuts, handler));
    },
    onUnsubscribe(paths, handler) {
        dispatch(unsubscribeChangesAction(paths, handler));
    },
});

const mapStateToProps = state => (
    {
        hasChanges: !transactionIsEmpty(state),
        lastLocation: selectLastLocation(state),
        shortcutPanelIsVisible: selectShortcutPanelIsVisible(state),
        applications: applicationsSelector(state),
    }
);

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(withRouter(FooterContainer));

export {
    filterApplications as _filterApplications,
    FooterContainer as _FooterContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
