import * as actionTypes from 'modules/shortcuts/actionTypes';

// NOTE(dbildstr): shortcut can be either a string of a list of strings
export const registerShortcut = (shortcut, handler) => ({
    type: actionTypes.SHORTCUT_REGISTER,
    shortcut,
    handler,
});

// NOTE(dbildstr): shortcut can be either a string of a list of strings
export const unregisterShortcut = (shortcut, handler) => ({
    type: actionTypes.SHORTCUT_UNREGISTER,
    shortcut,
    handler,
});
