import Loader from '@webui-one/components/Loading/Loader';
import Modal from '@webui-one/components/Modal/Modal';
import Checkbox from '@webui-one/components/ConfigRender/CheckBox/CheckBox';
import useLoadSettings from './useLoadSettings';
import ErrorBox from '@webui-one/components/ConfigRender/ErrorBox';
import PropTypes from 'prop-types';
import React, {
    Fragment,
} from 'react';
import { connect } from 'react-redux';
import { getApplicationsAsyncAction } from 'modules/applications/actions';
import { getTransChangesAsyncAction } from 'modules/transactions/actions';
import { APP_URI, DEFAULT_SETTINGS } from './constants';
import style from './SettingsStyle';

function Settings({ onRequestClose, onUpdateState }) {
    const {
        applications,
        error,
        isLoading,
        setHiddenAppHub,
        setShortcut,
        setDefault,
    } = useLoadSettings({ onDone: onUpdateState });
    const disabled = isLoading !== false;

    return (
        <Modal
            title={'GUI Settings'}
            onRequestClose={onRequestClose}
        >
            <div style={style.fullScreen}>
                <div style={style.header}>Application hub and shortcuts</div>
                {error && <ErrorBox error={error} />}
                <div style={style.section}>
                    Use the checkboxes to add shortcuts and meta-data to the webui-one
                    <a href="/webui-one/" style={style.link}> Application Hub</a>
                    .
                    <br />
                    Changes need to be committed to become persistent.
                </div>
                <div style={style.section}>
                    <button
                        type="button"
                        data-handle={'default-settings'}
                        disabled={disabled}
                        onClick={setDefault}
                        style={style[disabled ? 'disabledButton' : 'button']}
                    >
                        Use recommended settings
                    </button>
                </div>
                <div style={style.grid}>
                    <div style={style.title}>Application</div>
                    <div style={style.th}>Hub</div>
                    <div style={style.th}>Shortcut</div>
                    {DEFAULT_SETTINGS.map((defApp) => {
                        const { id, title, hidden, abbreviation, shortcut } = defApp;
                        const cdbApp = applications.find(app => app.id === id);
                        const isInHub = cdbApp ? !cdbApp.hidden : !hidden;
                        const isInFoot = cdbApp ? !!cdbApp.abbreviation && !!cdbApp.shortcut :
                            !!abbreviation && !!shortcut;

                        return (
                            <Fragment key={id}>
                                <div style={style.title}>{title}</div>
                                <Checkbox
                                    editable={!disabled}
                                    name={`${id}-hub`}
                                    onSetValue={() => setHiddenAppHub(id, !isInHub)}
                                    value={isInHub}
                                />
                                <Checkbox
                                    editable={!disabled}
                                    name={`${id}-shortcut`}
                                    onSetValue={() => setShortcut(id, !isInFoot)}
                                    value={isInFoot}
                                />
                            </Fragment>
                        );
                    })}
                </div>
                <div style={style.section}>
                    <a data-handle="custom-link" href={APP_URI} style={style.link}>
                        Customize the shortcuts in the configuration editor
                    </a>
                </div>
                {isLoading !== false && (
                    <div style={style.loader}><Loader color={style.loader.color} /></div>
                )}
            </div>
        </Modal>
    );
}

Settings.propTypes = {
    onRequestClose: PropTypes.func.isRequired,
    onUpdateState: PropTypes.func.isRequired,
};

const mapDispatchToProps = dispatch => ({
    onUpdateState() {
        dispatch(getTransChangesAsyncAction());
        dispatch(getApplicationsAsyncAction());
    },
});

export default connect(
    () => ({}),
    mapDispatchToProps,
)(Settings);

export {
    Settings as _Settings,
};
