/* eslint-disable import/prefer-default-export */
import { pathWithoutKeyInstances } from '@webui-one/core/keypath/kp-parser';
import { getDataThunk } from 'modules/data/actions';
import { selectPathFromLocationPathname } from 'modules/routing/selectors';
import {
    hideLoadingOverlayAction,
    showLoadingOverlayAction,
} from 'Common/LoadingOverlay/actions';
import {
    selectShowEmpty,
    selectShowOperdata,
    selectIncludeSubfolders,
} from 'ConfigurationEditor/reducer';

const shouldUpdate = (dependants, path) => (
    Array.isArray(dependants)
    && dependants.some(d => d.startsWith(pathWithoutKeyInstances(path)))
);

export const reEvaluateSchemaThunk = (dependents, forceUpdate = false, getData = getDataThunk) => (
    (dispatch, getState) => {
        const state = getState();
        const path = selectPathFromLocationPathname(state);

        if (!forceUpdate && shouldUpdate(dependents, path) !== true) {
            return Promise.resolve('should not update');
        }

        const showEmpty = selectShowEmpty(state);
        const showOperdata = selectShowOperdata(state);
        const includeSubfolders = selectIncludeSubfolders(state);

        dispatch(showLoadingOverlayAction());
        return dispatch(getData({
            path,
            includeSubfolders,
            showEmpty,
            showOperdata,
        })).finally(() => (
            dispatch(hideLoadingOverlayAction())
        ));
    }
);
